.buttonBlock button {
    padding: 1rem 2rem;
    margin: 1rem;
    border-radius: 1rem;
    position: fixed;
    bottom: -5rem;
    right: 1rem;
    transition: 0.3s all ease-in-out;
    background-color: black;
    color: white;
    border: none;
}

.buttonBlock button.show {
    bottom: 0rem;
}